import { useState } from "react";

// useNav
export const useNav = () => {
	const [navToggle, setNavToggle] = useState(false);

	// –§—É–Ω–∫—Ü—ñ—è –¥–ª—è –ø–µ—Ä–µ–º–∏–∫–∞–Ω–Ω—è —Å—Ç–∞–Ω—É –Ω–∞–≤—ñ–≥–∞—Ü—ñ—ó
	const navToggleHandler = () => {
		setNavToggle((prev) => !prev);
	};

	return { navToggle, navToggleHandler };
};